import { useEffect, useRef, useState } from "react";
import PasswordInput from "../common/PasswordInput";
import TextInput from "../common/TextInput";
import Profile from "./Profile";
import appConfig from "../common/config.json";
import * as profileApi from "./ProfileAPI";
import axios from "axios";
import {
  clearAppData,
  isAuthenticationError,
  parseError,
} from "../common/Utils";
import { toast } from "react-toastify";

function UpdateProfile(props) {
  const [dummy, setDummy] = useState({});
  const [name, setName] = useState(
    localStorage.getItem(appConfig.KEY_USER_NAME)
  );
  const [cred, setCred] = useState({
    oldPassword: "",
    newPassword: "",
  });
  const [loading, setLoading] = useState({
    name: false,
    password: false,
  });
  const [errors, setErrors] = useState({});

  const source = useRef(null);

  useEffect(() => {
    document.title = "VStore | Update Profile";
    return () => {
      source.current && source.current.cancel();
      source.current = null;
    };
  }, []);

  function getSource() {
    if (source.current == null) {
      const CancelToken = axios.CancelToken;
      source.current = CancelToken.source();
    }
    return source.current;
  }

  function handleChange({ target }) {
    if (target.name === "name") {
      setName(target.value);
    } else {
      setCred({
        ...cred,
        [target.name]: target.value,
      });
    }
  }

  function changeName(event) {
    event.preventDefault();
    if (!name) {
      return;
    }

    let oldName = localStorage.getItem(appConfig.KEY_USER_NAME);
    if (oldName === name) {
      return;
    }

    setLoading({
      ...loading,
      name: true,
    });
    profileApi
      .changeName(name, getSource().token)
      .then((resp) => {
        setLoading({
          ...loading,
          name: false,
        });
        if (resp.data.result === "SUCCESS") {
          localStorage.setItem(appConfig.KEY_USER_NAME, name);
          setDummy({});
          toast.success("Your name has been changed successfully.");
        } else {
          toast.error("Something went wrong.");
        }
      })
      .catch(handleApiError);
  }

  function changePassword(event) {
    event.preventDefault();
    const _errors = {};
    if (!cred.oldPassword || cred.oldPassword.length < 8) {
      _errors.oldPassword = "Password should not be less than 8 characters.";
    }

    if (!cred.newPassword || cred.newPassword.length < 8) {
      _errors.newPassword = "Password should not be less than 8 characters.";
    }

    setErrors(_errors);
    if (Object.keys(_errors).length > 0) {
      return;
    }

    setLoading({
      ...loading,
      password: true,
    });
    profileApi
      .changePassword(cred, getSource().token)
      .then((resp) => {
        setLoading({
          ...loading,
          password: false,
        });
        if (resp.data.result === "SUCCESS") {
          clearAppData();
          props.history.replace("/login");
          toast.success(
            "Your password has been changed successfully. Please log in again now."
          );
        } else {
          toast.error("Your old password is not correct.");
        }
      })
      .catch(handleApiError);
  }

  function handleApiError(error) {
    setLoading({
      name: false,
      password: false,
    });
    if (isAuthenticationError(error)) {
      clearAppData();
      props.history.replace("/login");
    } else {
      toast.error(parseError(error));
    }
  }

  return (
    <>
      <div className="col-lg-4 d-none d-md-none d-lg-block order-2">
        <Profile tab="setting" dummy={dummy} />
      </div>
      <div className="col-md-12 col-lg-8 order-1">
        <h5>Update Profile</h5>
        <hr />

        <div className="card border-0 rounded-lg shadow-cm mb-3">
          <div className="card-header font-weight-bold">Change Name</div>
          <div className="card-body">
            <form onSubmit={changeName}>
              <TextInput
                label="Your Name"
                name="name"
                type="text"
                placeholder="Your preferred name"
                value={name}
                onChange={handleChange}
              />
              <div className="clearfix">
                <button
                  type="submit"
                  className="btn btn-success float-right"
                  disabled={loading.name}
                >
                  {loading.name && (
                    <>
                      <span
                        className="spinner-border spinner-border-sm mr-1"
                        role="status"
                        aria-hidden="true"
                      ></span>
                      &nbsp;
                    </>
                  )}
                  Change
                </button>
              </div>
            </form>
          </div>
        </div>
        <div className="card border-0 rounded-lg shadow-cm">
          <div className="card-header font-weight-bold">Change Password</div>
          <div className="card-body">
            <form onSubmit={changePassword}>
              <PasswordInput
                label="Old Password"
                name="oldPassword"
                type="text"
                placeholder="Your current password"
                value={cred.oldPassword}
                onChange={handleChange}
                error={errors.oldPassword}
              />
              <PasswordInput
                label="New Password"
                name="newPassword"
                type="text"
                placeholder="Minimum 8 characters"
                value={cred.newPassword}
                onChange={handleChange}
                error={errors.newPassword}
              />
              <div className="clearfix">
                <button
                  type="submit"
                  className="btn btn-success float-right"
                  disabled={loading.password}
                >
                  {loading.password && (
                    <>
                      <span
                        className="spinner-border spinner-border-sm mr-1"
                        role="status"
                        aria-hidden="true"
                      ></span>
                      &nbsp;
                    </>
                  )}
                  Change
                </button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </>
  );
}

export default UpdateProfile;
