import TextInput from "../common/TextInput";
import Profile from "./Profile";
import IdeaImage from "./idea.png";
import DealImage from "./deal.png";
import { useEffect, useRef, useState } from "react";
import axios from "axios";
import { redeemVoucher } from "./ProfileAPI";
import {
  clearAppData,
  isAuthenticationError,
  parseError,
} from "../common/Utils";
import ErrorMessage from "../common/ErrorMessage";
import { toast } from "react-toastify";

function RedeemVoucherCode(props) {
  const [voucherCode, setVoucherCode] = useState("");
  const [inputError, setInputError] = useState("");
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");
  const source = useRef(null);

  function getSource() {
    if (source.current == null) {
      const CancelToken = axios.CancelToken;
      source.current = CancelToken.source();
    }
    return source.current;
  }

  useEffect(() => {
    document.title = "VStore | Redeem Voucher Code";
    return () => {
      source.current && source.current.cancel();
    };
  }, []);

  function handleChange({ target }) {
    setVoucherCode(target.value);
  }

  function handleSubmit(event) {
    event.preventDefault();
    setInputError("");
    setError("");
    if (!voucherCode) {
      setInputError("Voucher code is required.");
      return;
    }

    let body = { voucherCode: voucherCode };

    setLoading(true);
    redeemVoucher(body, getSource().token)
      .then((resp) => {
        setLoading(false);
        if (resp.data.result === "SUCCESS") {
          toast.success(resp.data.description);
        }
      })
      .catch((error) => {
        if (isAuthenticationError(error)) {
          clearAppData();
          props.history.replace("/login");
        } else {
          setLoading(false);
          setError(parseError(error));
        }
      });
  }

  return (
    <>
      <div className="col-lg-4 d-none d-md-none d-lg-block order-2">
        <Profile />
      </div>
      <div className="col-md-12 col-lg-8 order-1">
        <h5>Redeem Voucher Code</h5>
        <hr />
        <ErrorMessage message={error} />

        <div className="card border-0 rounded-lg shadow-cm mb-3">
          <div className="card-body">
            <p className="small mb-2">
              Enter the voucher code here and get bonus or reward. You cannot
              use same voucher code twice.
            </p>
            <form onSubmit={handleSubmit}>
              <TextInput
                label="Voucher Code"
                name="code"
                type="text"
                placeholder="Enter voucher code here"
                error={inputError}
                onChange={handleChange}
                value={voucherCode}
              />

              <div className="clearfix">
                <button
                  type="submit"
                  className="btn btn-success float-right px-3"
                  disabled={loading}
                >
                  {loading && (
                    <>
                      <span
                        className="spinner-border spinner-border-sm"
                        role="status"
                        aria-hidden="true"
                      ></span>
                      &nbsp;
                    </>
                  )}
                  Use
                </button>
              </div>
            </form>
          </div>
        </div>

        <div className="card border-0 rounded-lg shadow-cm">
          <div className="card-body">
            <div className="row mb-3">
              <img src={IdeaImage} alt="" width="60" className="mx-auto" />
            </div>
            <h6 className="text-center mb-3">Looking for more vouchers ?</h6>
            <p className="text-center small text-muted mb-3">
              Just do shopping or dining at VStore's parther shops or
              restaurants.
            </p>
            <div className="row">
              <a
                href="https://www.facebook.com/vstoremyanmar/"
                rel="noreferrer"
                target="_blank"
                className="btn btn-light mx-auto font-weight-medium"
              >
                <img src={DealImage} alt="" width="24" />
                <span className="my-auto ml-1">Our Partners</span>
              </a>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default RedeemVoucherCode;
