import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { useEffect, useReducer } from "react";
import {
  clearAppData,
  isAuthenticationError,
  parseError,
} from "../common/Utils";
import ErrorMessage from "../common/ErrorMessage";
import Loading from "../common/Loading";
import Profile from "../profile/Profile";
import { getNotifications } from "./NotificationAPI";

function reducer(state, action) {
  switch (action.type) {
    case "loading":
      return {
        loading: true,
      };
    case "success":
      return {
        data: action.data,
        loading: false,
      };
    case "error":
      return {
        loading: false,
        error: action.error,
      };
    default:
      return state;
  }
}

function Notification(props) {
  const [state, dispatch] = useReducer(reducer, { data: [], loading: false });

  useEffect(() => {
    let isCancel = false;
    document.title = "VStore | Notifications";
    dispatch({ type: "loading" });
    getNotifications()
      .then((resp) => {
        if (!isCancel) {
          dispatch({
            type: "success",
            data: resp.data,
          });
        }
      })
      .catch((error) => {
        if (isAuthenticationError(error)) {
          clearAppData();
          props.history.replace("/login");
        } else {
          !isCancel && dispatch({ type: "error", error: parseError(error) });
        }
      });
    return () => {
      isCancel = true;
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  function formatDate(millis) {
    let date = new Date(millis);
    let formatter = Intl.DateTimeFormat("en-US", {
      dateStyle: "medium",
      timeStyle: "short",
    });
    return formatter.format(date);
  }

  return (
    <>
      <div className="col-lg-4 d-none d-md-none d-lg-block order-2">
        <Profile />
      </div>
      <div className="col-md-12 col-lg-8 order-1">
        <div className="row">
          <div className="col-md-12">
            <div className="d-flex justify-content-between">
              <span className="h5 my-auto">Notifications</span>
              <button className="btn btn-sm btn-outline-danger">
                Delete All
              </button>
            </div>
            <hr />
            <Loading show={state.loading} />
            <ErrorMessage message={state.error} />

            {state.data && !state.data.length && (
              <div className="text-center text-muted">
                No notification found.
              </div>
            )}

            {state.data &&
              state.data.map((n) => {
                let read =
                  n.status === "READ" ? " text-muted" : " font-weight-bold";
                return (
                  <a
                    href="!#"
                    className="text-decoration-none"
                    key={n.notificationId}
                  >
                    <div className="card border-0 rounded-lg shadow-cm mb-3 col-md-12 list-group-item-action">
                      <div className="card-body px-0">
                        <div className="media">
                          <div
                            className="mr-3 bg-primary rounded-circle text-white"
                            style={{
                              width: "46px",
                              height: "46px",
                            }}
                          >
                            <div className="d-flex align-items-center justify-content-center h-100">
                              <FontAwesomeIcon
                                icon={["fas", "envelope"]}
                                size="lg"
                              />
                            </div>
                          </div>
                          <div className="media-body">
                            <label
                              className={
                                "my-0" +
                                (n.status === "READ"
                                  ? " font-weight-medium"
                                  : " font-weight-bold")
                              }
                            >
                              {n.title}
                            </label>
                            <div className={"mb-2 small" + read}>
                              {formatDate(n.notificationDate)}
                            </div>
                            <p className={"card-text mb-0" + read}>{n.body}</p>
                          </div>
                        </div>
                      </div>
                    </div>
                  </a>
                );
              })}
          </div>
        </div>
      </div>
    </>
  );
}

export default Notification;
