import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { useEffect, useRef, useState } from "react";
import { Link, useHistory } from "react-router-dom";
import appConfig from "../common/config.json";
import { getBalance, getProfile } from "./ProfileAPI";
import axios from "axios";

function UserDetail(props) {
  const [profile, setProfile] = useState({
    name: "---",
    mobile: "---",
  });

  const source = useRef(null);

  function getSource() {
    if (source.current == null) {
      const CancelToken = axios.CancelToken;
      source.current = CancelToken.source();
    }
    return source.current;
  }

  useEffect(() => {
    const _name = localStorage.getItem(appConfig.KEY_USER_NAME);
    const _mobile = localStorage.getItem(appConfig.KEY_MOBILE);
    if (!_name || !_mobile) {
      getProfile(getSource().token)
        .then((resp) => {
          const _data = {
            name: resp.data.name,
            mobile: resp.data.mobile,
          };
          localStorage.setItem(appConfig.KEY_USER_NAME, _data.name);
          localStorage.setItem(appConfig.KEY_MOBILE, _data.mobile);
          setProfile(_data);
        })
        .catch((error) => {
          console.log(error);
        });
    } else {
      const _data = {
        name: _name,
        mobile: _mobile,
      };
      setProfile(_data);
    }
    return () => {
      source.current && source.current.cancel();
      source.current = null;
    };
  }, [props.dummy]);

  return (
    <>
      <span className="d-block font-weight-bold text-center">
        {profile.name}
      </span>
      <div className="text-center text-muted mb-2">{profile.mobile}</div>
    </>
  );
}

function MainBalance() {
  const [balance, setBalance] = useState("");
  const [loading, setLoading] = useState(false);

  const source = useRef(null);

  function getSource() {
    if (source.current == null) {
      const CancelToken = axios.CancelToken;
      source.current = CancelToken.source();
    }
    return source.current;
  }

  useEffect(() => {
    const _balance = window.sessionStorage.getItem(appConfig.KEY_BALANCE);
    if (!_balance) {
      loadBalance();
    } else if (!isNaN(parseFloat(_balance))) {
      setBalance(
        parseFloat(_balance).toLocaleString("en", {
          minimumFractionDigits: 2,
        })
      );
    }
    return () => {
      if (source.current != null) source.current.cancel();
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  function loadBalance() {
    setLoading(true);
    getBalance(getSource().token)
      .then((resp) => {
        setLoading(false);
        window.sessionStorage.setItem(appConfig.KEY_BALANCE, resp.data.balance);
        setBalance(
          parseFloat(resp.data.balance).toLocaleString("en", {
            minimumFractionDigits: 2,
          })
        );
      })
      .catch((error) => {
        setLoading(false);
        console.log(error);
      });
  }

  return (
    <>
      <h5 className="text-center text-success mb-3">
        {!balance ? "---" : `${balance} VP`}
      </h5>
      <button
        className="btn btn-sm btn-outline-primary btn-block font-weight-medium"
        disabled={loading}
        onClick={() => loadBalance()}
      >
        {loading && (
          <>
            <span
              className="spinner-border spinner-border-sm mr-1"
              role="status"
              aria-hidden="true"
            ></span>
            &nbsp;
          </>
        )}
        Refresh Balance
      </button>
    </>
  );
}

