import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { useEffect, useRef, useState } from "react";
import { Link } from "react-router-dom";
import Profile from "./Profile";
import axios from "axios";
import { getBalance, getUnreadCount } from "./ProfileAPI";
import {
  clearAppData,
  isAuthenticationError,
  parseError,
} from "../common/Utils";
import { toast } from "react-toastify";
import appConfig from "../common/config.json";

function Dashboard(props) {
  const [profile, setProfile] = useState({
    name: "",
    mobile: "",
  });
  const [balance, setBalance] = useState("");
  const [unread, setUnread] = useState(0);
  const source = useRef(null);

  function getSource() {
    if (source.current == null) {
      const CancelToken = axios.CancelToken;
      source.current = CancelToken.source();
    }
    return source.current;
  }

  useEffect(() => {
    document.title = "VStore | Dashboard";
    const _name = localStorage.getItem(appConfig.KEY_USER_NAME);
    const _mobile = localStorage.getItem(appConfig.KEY_MOBILE);
    setProfile({
      name: _name,
      mobile: _mobile,
    });
    loadBalance();
    loadUnread();
    return () => {
      source.current && source.current.cancel();
      source.current = null;
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  function loadBalance() {
    getBalance(getSource().token)
      .then((resp) => {
        setBalance(
          parseFloat(resp.data.balance).toLocaleString("en", {
            minimumFractionDigits: 2,
          })
        );
      })
      .catch(handleError);
  }

  function loadUnread() {
    getUnreadCount(getSource().token)
      .then((resp) => {
        setUnread(resp.data.count);
      })
      .catch(handleError);
  }

  function handleError(error) {
    if (isAuthenticationError(error)) {
      clearAppData();
      props.history.replace("/login");
    } else {
      toast.error(parseError(error));
    }
  }

  function getMenuCard(icon, rotation, label) {
    return (
      <div className="card border-0 shadow-cm rounded-lg list-group-item-action h-100">
        <div className="card-body">
          <div className="row">
            <div className="col-md-12 col-lg-2 text-center">
              <FontAwesomeIcon
                icon={["fas", icon]}
                rotation={rotation}
                size="lg"
              />
            </div>
            <div className="col-md-12 col-lg-10 px-lg-0 text-center text-lg-left">
              <h6 className="d-lg-none"> </h6>
              <span className="font-weight-medium">{label}</span>
            </div>
          </div>
        </div>
      </div>
    );
  }

  return (
    <>
      <div className="col-lg-4 d-none d-md-none d-lg-block order-2">
        <Profile />
      </div>
      <div className="col-md-12 col-lg-8 order-1">
        <div className="card border-0 shadow-cm rounded-lg mb-3">
          <div className="card-body">
            <div className="media d-lg-none">
              <div className="bg-primary rounded-circle mr-3 px-2">
                <FontAwesomeIcon
                  icon={["far", "user"]}
                  size="2x"
                  className="text-white mx-1 my-2"
                />
              </div>
              <div className="media-body">
                <div className="">
                  <span className="h5 mt-0 mb-0">{profile.name}</span>
                  <Link
                    to="/update-profile"
                    className="btn btn-sm btn-outline-primary float-right"
                  >
                    <FontAwesomeIcon icon={["fas", "pen"]} />
                  </Link>
                </div>
                <span className="mb-0 text-muted">{profile.mobile}</span>
              </div>
            </div>
            <hr className="d-lg-none" />
            <div className="d-flex justify-content-between align-items-center d-lg-none">
              <span className="h5 mb-0">Balance</span>
              <span className="text-success font-weight-medium">
                {!!balance ? `${balance} VP` : "-----"}
              </span>
            </div>
            <div className="row d-none d-lg-block">
              <div className="media px-3">
                <div className="p-2 bg-primary rounded-circle mr-3">
                  <FontAwesomeIcon
                    icon={["fas", "wallet"]}
                    size="2x"
                    className="text-white m-2"
                  />
                </div>
                <div className="media-body">
                  <h4 className="text-dark mb-0">Your Balance</h4>
                  <h2 className="text-success mb-0">
                    {!!balance ? `${balance} VP` : "-----"}
                  </h2>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div className="row row-cols-2 mb-2">
          <div className="col mb-2 pr-1">
            <Link to="/transfer-point" className="text-decoration-none">
              {getMenuCard("sign-out-alt", 270, "Transfer VP")}
            </Link>
          </div>
          <div className="col mb-2 pl-1">
            <Link to="/deposit-options" className="text-decoration-none">
              {getMenuCard("sign-in-alt", 90, "Insufficient VP ?")}
            </Link>
          </div>

          <div className="col mb-2 pr-1">
            <Link to="/balance-history" className="text-decoration-none">
              {getMenuCard("inbox", 0, "VP Balance History")}
            </Link>
          </div>
          <div className="col mb-2 pl-1">
            <Link to="/transfer-history" className="text-decoration-none">
              {getMenuCard("inbox", 0, "VP Transfer History")}
            </Link>
          </div>
        </div>

        <div className="list-group">
          <Link
            to="/services"
            className="list-group-item list-group-item-action"
          >
            <div className="row">
              <div className="col">Buy Topup / E-Pin</div>
              <div className="col-auto text-secondary d-flex align-items-center">
                <FontAwesomeIcon icon={["fas", "chevron-right"]} />
              </div>
            </div>
          </Link>
          <Link
            to="/notifications"
            className="list-group-item list-group-item-action"
          >
            <div className="row">
              <div className="col">Notifications</div>
              <div className="col-auto text-secondary d-flex align-items-center">
                {!!unread && (
                  <span className="badge badge-danger badge-pill">
                    {unread}
                  </span>
                )}
                {!unread && <FontAwesomeIcon icon={["fas", "chevron-right"]} />}
              </div>
            </div>
          </Link>
        </div>
      </div>
    </>
  );
}

export default Dashboard;
