import TextInput from "../common/TextInput";
import Profile from "./Profile";
import IdeaImage from "./idea.png";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { useEffect, useRef, useState } from "react";
import axios from "axios";
import { redeemCoupon } from "./ProfileAPI";
import {
  clearAppData,
  isAuthenticationError,
  parseError,
} from "../common/Utils";
import { toast } from "react-toastify";
import ErrorMessage from "../common/ErrorMessage";

function UseCouponCode(props) {
  const [couponCode, setCouponCode] = useState("");
  const [inputError, setInputError] = useState("");
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");
  const source = useRef(null);

  function getSource() {
    if (source.current == null) {
      const CancelToken = axios.CancelToken;
      source.current = CancelToken.source();
    }
    return source.current;
  }

  useEffect(() => {
    document.title = "VStore | Use Coupon Code";
    return () => {
      source.current && source.current.cancel();
    };
  }, []);

  function handleChange({ target }) {
    setCouponCode(target.value);
  }

  function handleSubmit(event) {
    event.preventDefault();
    setInputError("");
    setError("");
    if (!couponCode) {
      setInputError("Coupon code is required.");
      return;
    }

    let body = { couponCode: couponCode };

    setLoading(true);

    redeemCoupon(body, getSource().token)
      .then((resp) => {
        setLoading(false);
        if (resp.data.result === "FAIL") {
          return;
        }

        switch (resp.data.benefitType) {
          case "VP":
            toast.success(
              `You have received bonus VP Points (${resp.data.vpPoints}) from coupon code (${couponCode})`
            );
            break;
          case "EPIN":
            let message = `Serial key :\n<strong>${resp.data.serialKey}</strong>\n\nDescription:\n${resp.data.description}`;
            toast.success(message);
            break;
          default:
            break;
        }
      })
      .catch((error) => {
        if (isAuthenticationError(error)) {
          clearAppData();
          props.history.replace("/login");
        } else {
          setLoading(false);
          setError(parseError(error));
        }
      });
  }

  return (
    <>
      <div className="col-lg-4 d-none d-md-none d-lg-block order-2">
        <Profile />
      </div>
      <div className="col-md-12 col-lg-8 order-1">
        <h5>Use Coupon Code</h5>
        <hr />
        <ErrorMessage message={error} />

        <div className="card border-0 rounded-lg shadow-cm mb-3">
          <div className="card-body">
            <p className="small mb-2">
              Enter the coupon code here and get bonus or reward. You cannot use
              same coupon code twice.
            </p>
            <form onSubmit={handleSubmit}>
              <TextInput
                label="Coupon Code"
                name="code"
                type="text"
                placeholder="Enter coupon code here"
                error={inputError}
                value={couponCode}
                onChange={handleChange}
              />

              <div className="clearfix">
                <button
                  type="submit"
                  className="btn btn-success float-right px-3"
                  disabled={loading}
                >
                  {loading && (
                    <>
                      <span
                        className="spinner-border spinner-border-sm"
                        role="status"
                        aria-hidden="true"
                      ></span>
                      &nbsp;
                    </>
                  )}
                  Use
                </button>
              </div>
            </form>
          </div>
        </div>

        <div className="card border-0 rounded-lg shadow-cm">
          <div className="card-body">
            <div className="row mb-3">
              <img src={IdeaImage} alt="" width="60" className="mx-auto" />
            </div>
            <h6 className="text-center mb-3">Looking for more coupons ?</h6>
            <p className="text-center small text-muted mb-3">
              It is ver easy ! Just like and follow our Facebook page and get
              coupon codes whenever we announce.
            </p>
            <div className="row">
              <a
                href="https://www.facebook.com/vstoremyanmar/"
                rel="noreferrer"
                target="_blank"
                className="btn btn-light mx-auto font-weight-medium"
              >
                <FontAwesomeIcon
                  icon={["fab", "facebook-square"]}
                  color="#3b5998"
                  size="lg"
                />
                <span className="my-auto ml-1">VStore Myanmar</span>
              </a>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default UseCouponCode;
