function IconLink(props) {
  return (
    <a
      href={props.href}
      className={"card-link" + props.wrapperClass}
      rel="noreferrer"
      target="_blank"
    >
      <FontAwesomeIcon icon={props.icon} size="lg" />
    </a>
  );
}

