import { useEffect, useRef, useState } from "react";
import TextArea from "../common/TextArea";
import TextInput from "../common/TextInput";
import ProductDetailHeader from "./ProductDetailHeader";
import axios from "axios";
import { purchaseTopup } from "./ProductAPI";
import ErrorMessage from "../common/ErrorMessage";
import {
  clearAppData,
  isAuthenticationError,
  parseError,
} from "../common/Utils";
import { useHistory } from "react-router-dom";
import HowToGuide from "./HowToGuide";

function PurchaseTopup(props) {
  const [form, setForm] = useState({
    listingId: "",
    mobile: "",
    remark: "",
  });
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");
  const [mobileError, setMobileError] = useState("");
  const source = useRef(null);
  const history = useHistory();

  useEffect(() => {
    return () => {
      if (source.current != null) source.current.cancel();
      source.current = null;
    };
  }, []);

  function getSource() {
    if (source.current == null) {
      const CancelToken = axios.CancelToken;
      source.current = CancelToken.source();
    }
    return source.current;
  }

  function handleChange({ target }) {
    const value = target.value;
    if (target.name === "mobile" && isNaN(value)) {
      return;
    }

    setForm({
      ...form,
      [target.name]: target.value,
    });
  }

  function handleSubmit(event) {
    event.preventDefault();

    setError("");
    setMobileError("");

    const regex = RegExp("^(09)\\d{7,12}$");
    if (!form.mobile || !regex.test(form.mobile)) {
      setMobileError("Mobile number is invalid.");
      return;
    }

    let body = {
      listingId: props.data.id,
      mobile: form.mobile,
      remark: form.remark,
    };

    setLoading(true);

    // props.onSuccess(props.data, {
    //   status: "PROCESSING",
    //   purchaseId: "98706754312897765",
    //   recipientMobile: form.mobile,
    // });

    purchaseTopup(body, getSource().token)
      .then((resp) => {
        setLoading(false);
        props.onSuccess(props.data, {
          status: resp.data.purchaseStatus,
          purchaseId: resp.data.purchaseId,
          recipientMobile: form.mobile,
        });
      })
      .catch((error) => {
        setLoading(false);
        if (isAuthenticationError(error)) {
          clearAppData();
          history.replace("/login");
        } else {
          setError(parseError(error));
        }
      });
  }

  return (
    <>
      <ErrorMessage message={error} />
      <ProductDetailHeader
        logo={props.data.photoUrl}
        title={props.data.title}
        amount={props.data.sellingPrice}
      />
      <div className="card border-0 rounded-lg shadow-cm mb-2">
        <form onSubmit={handleSubmit} className="card-body px-4 py-3">
          <div className="text-muted mb-2">
            Enter the mobile number that you want to topup.
          </div>
          <TextInput
            label="(+95)"
            name="mobile"
            type="tel"
            placeholder="Recipient's mobile"
            value={form.mobile}
            onChange={handleChange}
            error={mobileError}
          />
          <TextArea
            label="Remark"
            name="remark"
            type="text"
            placeholder=""
            value={form.remark}
            onChange={handleChange}
          />
          <div className="clearfix">
            <button
              type="submit"
              className="btn btn-success float-right"
              disabled={loading}
            >
              {loading && (
                <>
                  <span
                    className="spinner-border spinner-border-sm"
                    role="status"
                    aria-hidden="true"
                  ></span>
                  &nbsp;
                </>
              )}
              Purchase
            </button>
          </div>
        </form>
      </div>
      {props.data.howToGuideUrl && (
        <HowToGuide url={props.data.howToGuideUrl} />
      )}
    </>
  );
}

export default PurchaseTopup;
