import React, { useEffect, useReducer, useState } from "react";
import { Link } from "react-router-dom";
import { v4 as uuidv4 } from "uuid";
import config from "../../common/config.json";
import Welcome from "../../common/Welcome";
import { login } from "./LoginAPI";
import LoginForm from "./LoginForm";

function reducer(state, action) {
  switch (action.type) {
    case "loading":
      return {
        loading: true,
      };
    case "success":
      return {
        loading: false,
      };
    case "error":
      return {
        loading: false,
        error: action.error,
      };
    default:
      return state;
  }
}

function Login(props) {
  const [data, setData] = useState({
    mobile: "",
    password: "",
    token: "",
  });
  const [errors, setErrors] = useState({});

  const [state, dispatch] = useReducer(reducer, { loading: false });

  useEffect(() => {
    document.title = "VStore | Login";
  });

  function handleChange({ target }) {
    const value = target.value;
    if (target.name === "mobile" && isNaN(value)) {
      return;
    }
    setData({
      ...data,
      [target.name]: value,
    });
  }

  function handleSubmit(event) {
    event.preventDefault();
    if (!isFormValid()) return;
    dispatch({ type: "loading" });
    const token = uuidv4();
    data.token = token;
    localStorage.setItem(config.KEY_DEVICE_ID, token);
    login(data)
      .then((resp) => {
        // const parsed = JSON.parse(
        //   resp.data.replace(/"customerId":(\d+),/g, '"customerId":"$1",')
        // );
        if (resp.data.result !== "FAIL") {
          localStorage.setItem(config.KEY_TOKEN, resp.data.token);
          localStorage.setItem(config.KEY_USER_ID, resp.data.customerId);
          props.history.replace("/app");
        } else {
          dispatch({ type: "error", error: "Invalid login ID or password." });
        }
      })
      .catch((error) => {
        let msg = "Something went wrong.";
        console.log(error);
        if (error.response) {
          let data = error.response.data;
          msg = data.errorCode ? data.errorCode : data;
        }
        dispatch({ type: "error", error: msg });
      });
  }

  function isFormValid() {
    const _errors = {};
    if (!data.mobile) _errors.mobile = "Mobile number is invalid.";
    if (!data.password || data.password.length < 8) {
      _errors.password = "Password should not be less than 8 characters.";
    }
    setErrors(_errors);
    return Object.keys(_errors).length === 0;
  }

  return (
    <>
      <div className="my-4 col-lg-7 d-none d-md-none d-lg-block">
        <Welcome />
      </div>
      <div className="col-md-8 col-lg-5 my-5 mx-auto">
        <div className="card shadow border-0">
          <div className="card-body">
            <h1 className="text-center mb-4 text-primary logo-text">vstore</h1>
            {state.error && (
              <div className="alert alert-danger mb-3 border-0" role="alert">
                {state.error}
              </div>
            )}
            <LoginForm
              data={data}
              onChange={handleChange}
              onSubmit={handleSubmit}
              errors={errors}
              loading={state.loading}
            />
            <div className="row mt-4">
              <div className="mx-auto small">
                <span>Don't have an account?</span>
                <Link to="/sign-up" className="card-link text-primary ml-1">
                  Sign Up
                </Link>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default Login;
